import React, { Fragment, useState } from 'react';
import Loader from '../../common/Loader/Loader';
import s from './ProfileInfo.module.css';
import ProfileStatusFunc from './ProfileStatus/ProfileStatusFunc';
import defaultAvatar from '../../../assets/images/defautltUser.png';
import EditProfileData from './EditProfileData/EditProfileData';
import { reduxForm } from 'redux-form';

let EditProfileDataRedux = reduxForm({ form: 'edit-profile' })(EditProfileData);

function ProfileInfo(props) {
  let [editMode, setEditMode] = useState(false);

  let handleUploadPhoto = (e) => {
    if (e.target.files.length) {
      props.uploadPhoto(e.target.files[0]);
    }
  };

  let handleEditMode = () => {
    setEditMode(true);
  };

  let onSubmit = (form) => {
    props
      .saveProfile(form)
      .then(() => {
        setEditMode(false);
      })
      .catch(() => {});
  };

  if (!props.profile) {
    return <Loader />;
  } else {
    return (
      <div>
        <div className={s.img}>
          <img
            src={props.profile.photos.large || defaultAvatar}
            alt="nice avatar"
          />
        </div>
        {props.isOwner && <input type="file" onChange={handleUploadPhoto} />}

        {props.profile.fullName && <div>{props.profile.fullName} </div>}

        <ProfileStatusFunc
          {...props}
          status={props.status}
          setStatus={props.setStatus}
          updateStatus={props.updateStatus}
        />

        {editMode ? (
          <EditProfileDataRedux
            initialValues={props.profile}
            onSubmit={onSubmit}
            profile={props.profile}
          />
        ) : (
          <ProfileData
            isOwner={props.isOwner}
            profile={props.profile}
            handleEditMode={handleEditMode}
          />
        )}
      </div>
    );
  }
}

let ProfileData = (props) => {
  return (
    <>
      {props.isOwner ? (
        <button onClick={props.handleEditMode}></button>
      ) : null}
      {props.profile.lookingForAJob ? (
        <div> </div>
      ) : (
        <div>  </div>
      )}

      {Object.values(props.profile.contacts).map((contact) => {
        return contact !== null ? <div key={contact}>{contact}</div> : null;
      })}

      <span>: </span>

      {Object.entries(props.profile.contacts).map((key) => {
        return (
          <Fragment key={key}>
            <div>{key[1] !== null ? <div>{key[0]}</div> : null}</div>
          </Fragment>
        );
      })}
    </>
  );
};

export default ProfileInfo;
