import { usersItemsType } from '../Types/types';
import { UsersAPI } from '../api/api';

let FOLLOW = 'FOLLOW';
let UNFOLLOW = 'UNFOLLOW';
let SET_USERS = 'SET_USERS';
let SET_PAGE = 'SET_PAGE';
let SET_TOTAL_USERS_COUNT = 'SET_TOTAL_USERS_COUNT';
let TOGGLE_IS_FETCHING = 'TOGGLE_IS_FETCHING';
let TOGGLE_IS_FOLLOWING = 'TOGGLE_IS_FOLLOWING';

// type initialStateType = {
//   usersItems: usersItemsType
//   totalUsers: null | number
//   pageSize: number 
//   pageSelected: number
//   isFetching: boolean
//   isFollowingInProgress: Array<number>
// }

let initialState = {
  usersItems: [] as Array<usersItemsType>,
  totalUsers: null, //19
  pageSize: 4, //4
  pageSelected: 1,
  isFetching: false,
  isFollowingInProgress: [] as Array<number>,
};

type initialStateType = typeof initialState;

export default function userReducer(state = initialState, action: any): initialStateType {
  switch (action.type) {
    case FOLLOW:
      return {
        ...state,
        usersItems: state.usersItems.map((user) => {
          if (user.id === action.id) {
            return { ...user, followed: true };
          } else {
            return user;
          }
        }),
      };
    case UNFOLLOW:
      return {
        ...state,
        usersItems: state.usersItems.map((user) => {
          if (user.id === action.id) {
            return { ...user, followed: false };
          } else {
            return user;
          }
        }),
      };
    case SET_USERS:
      return {
        ...state,
        usersItems: action.users,
      };

    case SET_PAGE:
      return {
        ...state,
        pageSelected: action.pageSelected,
      };
    case SET_TOTAL_USERS_COUNT:
      return {
        ...state,
        totalUsers: action.totalCount,
      };
    case TOGGLE_IS_FETCHING:
      return {
        ...state,
        isFetching: action.isFetching,
      };
    case TOGGLE_IS_FOLLOWING:
      return {
        ...state,
        isFollowingInProgress: action.isFollowingInProgress
          ? [...state.isFollowingInProgress, action.userId]
          : state.isFollowingInProgress.filter((id) => id !== action.userId),
      };
    default:
      return state;
  }
}

type followType = {
  type: typeof FOLLOW
  id: number
}

export const follow = (id: number): followType => ({
  type: FOLLOW,
  id: id,
});

type unfollowType = {
  type: typeof UNFOLLOW
  id: number
}

export const unfollow = (id: number): unfollowType => ({
  type: UNFOLLOW,
  id: id,
});

type setusersType = {
  type: typeof SET_USERS
  users: Array<usersItemsType>
}

export const setusers = (users: Array<usersItemsType>): setusersType => ({
  type: SET_USERS,
  users: users,
});

type setpageType = {
  type: typeof SET_PAGE
  pageSelected: number
}

export const setpage = (pageSelected: number): setpageType => ({
  type: SET_PAGE,
  pageSelected: pageSelected,
});

type setTotalUsersCountType = {
  type: typeof SET_TOTAL_USERS_COUNT
  totalCount: number
}

export const setTotalUsersCount = (totalCount: number): setTotalUsersCountType => ({
  type: SET_TOTAL_USERS_COUNT,
  totalCount: totalCount,
});

type toggleIsFetchingType = {
  type: typeof TOGGLE_IS_FETCHING
  isFetching: boolean
}


export const toggleIsFetching = (isFetching: boolean): toggleIsFetchingType => ({
  type: TOGGLE_IS_FETCHING,
  isFetching: isFetching,
});

type toggleIsFollowingType = {
  type: typeof TOGGLE_IS_FOLLOWING
  isFollowingInProgress: boolean
  userId: number
}


export const toggleIsFollowing = (isFollowingInProgress: boolean, userId: number): toggleIsFollowingType => ({
  type: TOGGLE_IS_FOLLOWING,
  isFollowingInProgress: isFollowingInProgress,
  userId: userId,
});

export const getUsers = (pageSelected: number, pageSize: number) => async (dispatch: any) => {
  dispatch(toggleIsFetching(true));

  let data = await UsersAPI.getUsers(pageSelected, pageSize);
  dispatch(toggleIsFetching(false));
  dispatch(setusers(data.items));
  dispatch(setTotalUsersCount(data.totalCount));
  dispatch(setpage(pageSelected));
};

export const followAPI = (userId: number) => async (dispatch: any) => {
  dispatch(toggleIsFollowing(true, userId));
  let response = await UsersAPI.follow(userId);
  if (response.data.resultCode === 0) {
    dispatch(follow(userId));
  }
  dispatch(toggleIsFollowing(false, userId));
};

export const unfollowAPI = (userId: number) => async (dispatch: any) => {
  dispatch(toggleIsFollowing(true, userId));
  let response = await UsersAPI.unfollow(userId);
  if (response.data.resultCode === 0) {
    dispatch(unfollow(userId));
  }
  dispatch(toggleIsFollowing(false, userId));
};
