let ADD_MESSAGE = 'ADD-MESSAGE';


type initialStateType = {
  dialogItems: 
    {
      id: number
      name: string
    }[]
  
  messageItems: 
    {
      id: number
      message: string
    }[]
  
}

let initialState: initialStateType = {
  dialogItems: [
    { id: 1, name: '' },
    { id: 2, name: '' },
    { id: 3, name: '' },
    { id: 4, name: '' },
    { id: 5, name: '' },
  ],
  messageItems: [
    { id: 1, message: 'yoo' },
    { id: 2, message: ' ' },
    { id: 3, message: '  ' },
  ],
};

export default function dialogsReducer(state = initialState, action: any): initialStateType {
  switch (action.type) {
    case ADD_MESSAGE: {
      return {
        ...state,
        messageItems: [
          ...state.messageItems,
          {
            id: 6,
            message: action.newMessage,
          },
        ],
      };
    }

    default:
      return state;
  }
}

type addMessgaeActionCreatorType = {
  type: typeof ADD_MESSAGE
  newMessage: string
}

export const addMessgaeActionCreator = (newMessage: string): addMessgaeActionCreatorType => ({
  type: ADD_MESSAGE,
  newMessage,
});
