import React from 'react';
import { NavLink } from 'react-router-dom';
import s from './User.module.css';
import defaultUser from '../../../assets/images/defautltUser.png';

const User = ({ item, isFollowingInProgress, unfollowAPI, followAPI }) => {
  return (
    <div key={item.id} className={s.singleUser}>
      <div>
        <div>
          <NavLink to={'/profile/' + item.id}>
            <img
              src={item.photos.small !== null ? item.photos.small : defaultUser}
              alt="avatar"
              className={s.img}
            />
          </NavLink>
        </div>
        <div>
          {item.followed ? (
            <button
              disabled={isFollowingInProgress.find((id) => id === item.id)}
              onClick={() => {
                unfollowAPI(item.id);
              }}
            >
              
            </button>
          ) : (
            <button
              disabled={isFollowingInProgress.find((id) => id === item.id)}
              onClick={() => {
                followAPI(item.id);
              }}
            >
              
            </button>
          )}
        </div>
      </div>

      <div>
        <div>{item.name}</div>
        <div>{item.status} </div>
      </div>
    </div>
  );
};

export default User;
