import { stopSubmit } from 'redux-form';
import { ProfileAPI, UsersAPI } from '../api/api';
import { photosType, profileType } from '../Types/types';

let ADD_POST = 'ADD-POST';
let SET_USER_PROFILE = 'SET_USER_PROFILE';
let SET_STATUS = 'SET_STATUS';
let SET_AVATAR = 'SET_AVATAR';




type initialStateType = {
  postItems: {
    id: number
    message: string
    likes: number | null
  }[]

  profile: null | profileType
  status: string
  
}

let initialState: initialStateType = {
  postItems: [
    { id: 1, message: ' !', likes: 3 },
    { id: 2, message: '  ', likes: 6 },
    { id: 3, message: ' ', likes: 1 },
  ],

  profile: null,
  status: '',
  
};

export default function profileReducer(state = initialState, action: any): initialStateType {
  switch (action.type) {
    case ADD_POST: {
      return {
        ...state,
        postItems: [
          {
            id: 5,
            message: action.mypostForm,
            likes: 0,
          },
          ...state.postItems,
        ],
      };
    }

    case SET_USER_PROFILE: {
      return {
        ...state,
        profile: action.profile,
      };
    }
    case SET_STATUS: {
      return {
        ...state,
        status: action.status,
      };
    }

    case SET_AVATAR: {
      return {
        ...state,
        profile: { ...state.profile, photos: action.photo } as profileType,
      };
    }

    default:
      return state;
  }
}

export type addPostType ={
  type: typeof ADD_POST
  mypostForm: any
}

export const addPost = (mypostForm: any): addPostType => ({
  type: ADD_POST,
  mypostForm,
});

export type setUserProfileType ={
  type: typeof SET_USER_PROFILE
  profile: profileType
}


export const setUserProfile = (profile: profileType): setUserProfileType => ({
  type: SET_USER_PROFILE,
  profile: profile,
});

export type updateStatusType ={
  type: typeof SET_STATUS
  status: string
}

export const updateStatus = (status: string): updateStatusType => ({
  type: SET_STATUS,
  status: status,
});

export type savePhotoType ={
  type: typeof SET_AVATAR
  photo: photosType
}

export const savePhoto = (photo: photosType): savePhotoType => ({
  type: SET_AVATAR,
  photo: photo,
});

export const getUserProfile = (userId: number) => async (dispatch: any) => {
  try {
    let response = await UsersAPI.getProfile(userId);
    dispatch(setUserProfile(response.data));
  } catch (error: any) {
    console.log(error.message);
  }
};

export const getStatus = (userId: number) => async (dispatch: any) => {
  try {
    let response = await ProfileAPI.getStatus(userId);
    dispatch(updateStatus(response.data));
  } catch (error: any) {
    console.log(error.message);
  }
};

export const setStatus = (status: string) => async (dispatch: any) => {
  try {
    dispatch(updateStatus(status));
    let response = await ProfileAPI.setStatus(status);
    if (response.data.resultCode === 0) {
      dispatch(updateStatus(status));
    }
  } catch (error: any) {
    console.log(error.message);
  }
};

export const uploadPhoto = (photo: photosType) => async (dispatch: any) => {
  try {
    let response = await ProfileAPI.uploadPhoto(photo);

    if (response.data.resultCode === 0) {
      dispatch(savePhoto(response.data.data.photos));
    }
  } catch (error: any) {
    console.log(error.message);
  }
};
export const saveProfile = (form: any) => async (dispatch: any, getState: any) => {
  try {
    const userId = getState().auth.userId;

    let response = await ProfileAPI.saveProfile(form);

    if (response.data.resultCode === 0) {
      dispatch(getUserProfile(userId));
    } else {
      let errorMessage =
        response.data.messages.length > 0
          ? response.data.messages[0]
          : 'Something wrong, I can feel it';

      dispatch(stopSubmit('edit-profile', { _error: errorMessage }));
      return Promise.reject(errorMessage);
    }
  } catch (error: any) {
    console.log(error.message);
  }
};
