import { AuthAPI, SecurityAPI } from '../api/api';
import { stopSubmit } from 'redux-form';

const SET_USER_DATA = 'SET_USER_DATA';
const GET_CAPTCHA_URL = 'GET_CAPTCHA_URL';

export type initialStateType = {
  userId: null | number;
  email: null | string;
  login: null | string;
  isAuth: boolean;
  captchaUrl: null | string;
};


let initialState = {
  userId: null,
  email: null,
  login: null,
  isAuth: false,
  captchaUrl: null,
};

export default function authReducer(
  state = initialState,
  action: any,
): initialStateType {
  switch (action.type) {
    case SET_USER_DATA: {
      return {
        ...state,
        ...action.data,
        isAuth: action.data.isAuth,
      };
    }
    case GET_CAPTCHA_URL: {
      return {
        ...state,
        // ...action.data,
        captchaUrl: action.captchaUrl,
      };
    }

    default:
      return state;
  }
}

type setUserDataType = {
  type: typeof SET_USER_DATA;
  data: {
    userId: number | null;
    email: string | null;
    login: string | null;
    isAuth: boolean;
  };
};

export const setUserData = (
  userId: number | null,
  email: string | null,
  login: string | null,
  isAuth: boolean,
): setUserDataType => ({
  type: SET_USER_DATA,
  data: { userId, email, login, isAuth },
});

type getCaptchaUrlACType = {
  type: typeof GET_CAPTCHA_URL;
  captchaUrl: string;
};

export const getCaptchaUrlAC = (captchaUrl: string): getCaptchaUrlACType => ({
  type: GET_CAPTCHA_URL,
  captchaUrl: captchaUrl,
});

export const getUserData = () => async (dispatch: any) => {
  const response = await AuthAPI.me();
  if (response.data.resultCode === 0) {
    let { id, email, login } = response.data.data;
    dispatch(setUserData(id, email, login, true));
  }
};

export const login =
  (email: string, password: string, remember: boolean, captcha: string) =>
  async (dispatch: any) => {
    let response = await AuthAPI.login(email, password, remember, captcha);
    if (response.data.resultCode === 0) {
      dispatch(getUserData());
    } else {
      if (response.data.resultCode === 10) {
        dispatch(getCaptchaUrl());
      }
      let errorMessage =
        response.data.messages.length > 0
          ? response.data.messages[0]
          : 'Something wrong, I can feel it';
      dispatch(stopSubmit('login', { _error: errorMessage }));
    }
  };
export const getCaptchaUrl = () => async (dispatch: any) => {
  const response = await SecurityAPI.getCaptchaUrl();
  const captchaUrl = response.data.url;
  dispatch(getCaptchaUrlAC(captchaUrl));
};

export const logout =
  (email: string, password: string, remember: boolean) =>
  async (dispatch: any) => {
    let response = await AuthAPI.logout();
    // let response = await AuthAPI.logout(email, password, remember);
    if (response.data.resultCode === 0) {
      dispatch(setUserData(null, null, null, false));
    }
  };
