import React, { Fragment } from 'react';
import { Field } from 'redux-form';
import { required } from '../../../../utils/validator';
import {
  Checkbox,
  Input,
  TextArea,
} from '../../../common/FormControls/Formcontrol';
import s from '../../../Login/Login.module.css';

let EditProfileData = (props) => {
  return (
    <form onSubmit={props.handleSubmit}>
      <div>
        <button></button>
      </div>

      {props.error ? <div className={s.errorLog}>{props.error}</div> : null}

      <div>
        <span> : </span>
        <Field
          validate={[required]}
          component={Input}
          name={'fullName'}
          type="text"
          placeholder=" "
        />
      </div>

      <div>
        <span> : </span>

        <Field name={'lookingForAJob'} component={Checkbox} type="checkbox" />
      </div>

      <div>
        <span> : </span>

        <Field
          validate={[required]}
          component={TextArea}
          name={'lookingForAJobDescription'}
          type="text"
          placeholder="  "
        />
      </div>

      <div>
        <span> : </span>

        <Field
          validate={[required]}
          component={TextArea}
          name={'aboutMe'}
          type="text"
          placeholder="  "
        />
      </div>

      {Object.keys(props.profile.contacts).map((key) => {
        return (
          <Fragment key={key}>
            <span>{key}</span>
            <Field
              component={Input}
              name={'contacts.' + key}
              type="text"
              placeholder={key}
            />
          </Fragment>
        );
      })}
    </form>
  );
};

export default EditProfileData;
